import React, { useState } from 'react';
import { StyleSheet, SafeAreaView, FlatList, Text, Platform, View, Button, TextInput } from 'react-native';
import { CheckBox } from '@rneui/themed';

// stylesheet (similar to css)
const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    paddingTop: Platform.OS === 'android' ? 25 : 0,
    padding: 20,
  },
  taskContainer: {
    flexDirection: 'row',
    backgroundColor: '#fff',
    alignItems: 'center',
    marginBottom: 10,
    marginTop: 10,
  },
  taskText: {
    marginLeft: 10,
    fontSize: 18,
    padding: 20,
  },
  inputContainer: {
    backgroundColor: '#fff',
    marginTop: 20,
    flexDirection: 'row',
    alignItems: 'center',
    padding: 20,
  },
  input: {
    flex: 1,
    marginRight: 0,
  },
  addSection: {
    flex: 1,
    marginTop: 0,
  },
});

export default function App() {
  const [tasks, setTasks] = useState([
    { key: '1', title: 'Go grocery shopping', completed: false },
    { key: '2', title: 'Feed the dog', completed: false },
    { key: '3', title: 'Water the plants', completed: false },
  ]);
  const [newTask, setNewTask] = useState('');

  const toggleCheckBox = (key) => {
    setTasks((prevTasks) =>
      prevTasks.map((task) =>
        task.key === key ? { ...task, completed: !task.completed } : task
      )
    );
  };
//adding new task 
  const addNewTask = () => {
    if (newTask.trim()) {
      const newTaskObj = {
        key: (tasks.length + 1).toString(),
        title: newTask,
        completed: false,
      };
      setTasks((prevTasks) => [...prevTasks, newTaskObj]);
      setNewTask('');
    }
  };

  const renderItem = ({ item }) => {
    return (
      <View style={styles.taskContainer}>
        <CheckBox
          checked={item.completed}
          onPress={() => toggleCheckBox(item.key)}
          containerStyle={{ marginRight: 10 }}
        />
        <Text
          style={[
            styles.taskText,
            item.completed && {
              textDecorationLine: 'line-through',
              textDecorationStyle: 'solid',
            },
          ]}
        >
          {item.title}
        </Text>
      </View>
    );
  };

  return (
    <SafeAreaView style={styles.container}>
      <FlatList
        data={tasks}
        renderItem={renderItem}
        keyExtractor={(item) => item.key}/>
    <View style={styles.addSection}>
      <View style={styles.inputContainer}>
        <TextInput
          placeholder="Add New To-Do Task"
          value={newTask}
          onChangeText={(text) => setNewTask(text)}
          style={styles.input}
        />
        <Button title="Add" onPress={addNewTask} />
      </View>
      </View>
    </SafeAreaView>
  );
}
